@use 'styles/colors';
@import 'styles/base';

:host {
  display: block;

  .top-section {
    max-width: 460px;
  }

  .content-section {
    margin-top: 30px;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    gap: 40px;
  }

  .page-title {
    @extend %page-title;
    padding-bottom: 20px;
  }

  .page-description {
    @extend %page-description;
    padding-top: 0;
  }

  .page-card {
    @extend %page-card;

    width: 100%;
    max-width: 540px;
  }

  .card-title {
    @extend %card-title;
    padding-bottom: 0;
  }

  .section-divider {
    @extend %section-divider;
  }
}
